<template>
  <div class="counter">
    <button @click="sub">-</button><span>{{num}}</span
    ><button @click="add">+</button>
  </div>
</template>

<script>
import bus from "@/components/eventBus.js"

export default {
  props:{
    id:{
      type:Number,
      default:0
    },
    num:{
      type:Number,
      default:0
    }
  },
  methods:{
    add(){
      const obj={id:this.id,counts:this.num+1}
      // console.log(obj);
      bus.$emit('share',obj)
    },
    sub(){
      if(this.num===0)return false
      const obj={id:this.id,counts:this.num-1}
      // console.log(obj);
      bus.$emit('share',obj)
    }
  },
  data(){
    return {
    }
  }
};
</script>

<style lang="less" scoped>
.counter {
  width: 70px;
  height: 40px;
  // background-color: yellow;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  button {
    width: 10px;
    height: 18px;
    padding-right: 13px;
    padding-bottom: 16px;
    background-color: skyblue;
    color: rgb(238, 255, 0);
    // text-align: center;
  }
}
</style>