<template>
  <div class="footer">
    <input
      type="checkbox"
      :checked="fullState"
      @change="fullStateChange"
    /><span class="allChoose">全选</span>
    <span class="sum"
      >总价：<span>￥{{ allPrice }}元</span></span
    >
    <button>结算({{ allCounts }})</button>
  </div>
</template>

<script>
export default {
  props: {
    fullState: {
      type: Boolean,
      default: false,
    },
    allPrice: {
      type: Number,
      default: 0,
    },
    allCounts: {
      type: Number,
      default: 0,
    },
  },
  methods: {
    fullStateChange(e) {
      // console.log(e.target.checked);
      this.$emit("fullStateChange", { newFullState: e.target.checked });
    },
  },
};
</script>

<style lang="less" scoped>
.footer {
  display: flex;
  position: fixed;
  justify-content: space-between;
  align-items: center;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 35px;
  background-color: white;
  font-size: 15px;
  .allChoose {
    margin-left: -65px;
  }
  .sum span {
    color: red;
  }
  button {
    width: 85px;
    height: 35px;
    border: 1px solid;
    border-radius: 20px;
    background-color: pink;
  }
}
</style>