<template>
  <div class="goods">
    <ul>
      <li>
        <div class="check"><input type="checkbox" :checked="state" @change="stateChange"/></div>
        <div class="pic"><img :src="image" alt="" height="100px" /></div>
        <div class="price">
          <div>
            <h6>{{title}}</h6>
            <Counter :num="count" :id="id"></Counter>
          </div>
          <p>
            ￥<span>{{price}}</span>
          </p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import Counter from '@/components/Counter/Counter.vue'
export default {
  components:{
    Counter
  },
 props:{
   id:{
     type:Number,
     default:0

   },
   count:{
     type:Number,
     default:0

   },
   title:{
     type:String,
     default:''
   },
   image:{
     type:String,
     default:''
   },
   state:{
     type:Boolean,
     default:false

   },
   price:{
     type:Number,
     default:0
   }
 },
 methods:{
   stateChange(e){
    //  console.log(e);
    const newState=e.target.checked
    // console.log(this);
    this.$emit('stateChange',{id:this.id,newState:newState})
   }
 }
};
</script>

<style lang='less' scoped>
.goods {
  //   margin-top: 0px;
  width: 100%;
  height: 100px;
  //   background-color: pink;
  //   border: 1px solid #000;
  ul li {
    height: 100px;
    // margin-top: -10px;
    margin-bottom: 5px;

    display: flex;
    align-items: center;
    // justify-content: space-between;
    list-style: none;

    .check {
      margin-left: -30px;
      margin-right: 10px;
    }
    .price {
      height: 100px;
      display: flex;
      div {
        margin-top: -25px;
      }
      p {
        align-self: flex-end;
        color: red;
      }
    }
  }
}
</style>