<template>
  <div class="shopping">
    <Header title="购物车案例"></Header>
    <Goods
      v-for="item in goodsList"
      :key="item.id"
      :title="item.goods_name"
      :image="item.goods_img"
      :state="item.goods_state"
      :price="item.goods_price"
      :id="item.id"
      :count="item.goods_count"
      @stateChange="getNewState"
    ></Goods>
    <Footer
      :fullState="isFull"
      :allPrice="isAllPrice"
      :allCounts="isAllCounts"
      @fullStateChange="getNewFullState"
    ></Footer>
  </div>
</template>

<script>
import Header from "@/components/Header/Header.vue";
import Goods from "@/components/Goods/Goods.vue";
import axios from "axios";
import Footer from "@/components/Footer/Footer.vue";
import bus from "@/components/eventBus.js";

export default {
  components: {
    Header,
    Goods,
    Footer,
  },
  created() {
    // console.log(this);
    this.initCartList();
    bus.$on("share", (val) => {
      // console.log(val);
      this.goodsList.some((item) => {
        if (item.id === val.id) {
          item.goods_count = val.counts;
        }
      });
    });
  },
  methods: {
    initCartList() {
      axios.get("https://www.escook.cn/api/cart").then((val) => {
        // console.log(val);
        this.goodsList = val.data.list;
        console.log(this.goodsList);
      });
    },
    getNewState(e) {
      console.log(e);
      // console.log(this.goodsList.id);
      this.goodsList.some((item, index) => {
        if (item.id === e.id) {
          item.goods_state = e.newState;
        }
      });
    },
    getNewFullState(e) {
      // console.log(e);
      this.goodsList.forEach((item) => {
        item.goods_state = e.newFullState;
      });
    },
  },
  data() {
    return {
      goodsList: [],
    };
  },
  computed: {
    isFull() {
      return this.goodsList.every((item) => item.goods_state);
    },
    isAllPrice() {
      return this.goodsList
        .filter((item) => item.goods_state)
        .reduce((amount, item) => {
          return (amount += item.goods_count * item.goods_price);
        }, 0);
    },
    isAllCounts() {
      return this.goodsList
        .filter((item) => item.goods_state)
        .reduce((counts,item) => {
          return (counts += item.goods_count);
        },0);
    },
  },
};
</script>

<style lang='less' scoped>
.shopping {
  padding-top: 35px;
  padding-bottom: 35px;
}
</style>